export function renderGallery(images) {
    return images
    .map(
        ({ webformatURL, largeImageURL, tags }) => `
        <div class="photo-card">
        <a href="${largeImageURL}">
        <img src="${webformatURL}" alt="${tags}" loading="lazy" />
        </a>
        </div>`,
    )
    .join('');
}
